class Config_Quick {
    gameTime: number = 60;

    constructor(){}

    changeGameTimeValue (state: boolean) {
        switch (state) {
            case true:
                if (this.gameTime < 300) {
                    this.gameTime += 10;
                }
                break;
            case false:
                if (this.gameTime > 60) {
                    this.gameTime -= 10;
                }
            default:
                break;
        }
        
    }
}

export default Config_Quick;