class Config_Triple {
    roundTime: number = 30;
    difficulty: number = 1;

    constructor() {}

    changeGameTimeValue (state: boolean) {
        switch (state) {
            case true:
                if (this.roundTime < 120) {
                    this.roundTime += 10;
                }
                break;
            case false:
                if (this.roundTime > 30) {
                    this.roundTime -= 10;
                }
            default:
                break;
        }   
    }

    changeDifficultyValue (difficulty: number) {
        this.difficulty = difficulty;
    }
}

export default Config_Triple;