class Category {
    selectedCategory: number = 0;
    selectedPoint: number = 0;
    blur: boolean = false;

    constructor() {}

    setCategory (number: number) {
        this.selectedCategory = number;
    }

    setPoint (number: number) {
        this.selectedPoint = number;
    }

    changeBlur (state: boolean) {
        this.blur = state;
    }

}

export default Category;