import Team from "./team";
import getdb from "../database/getdb";
import { configQuickInstance,teamsInstance } from ".";
import Teams from "./teams";

class Game_Quick {
  started: boolean = false;
  gameFinished: boolean = false;
  teams: Team[];
  turn: number;
  word: string = "بازی شروع نشده است";
  time: number = 0;
  winner: string = '';
  interval: any;

  constructor() {
    this.teams = teamsInstance.teams;
    this.turn = 1;
  }

  configure(configurations: Teams) {
    this.teams = configurations.teams;
    this.gameFinished = false;
  }

  setWord = (): Promise<any> => {
    let iWillSetWord = new Promise((resolve, reject) => {
      let randomCategory = Math.floor(Math.random()*20+1);
      let randomPoint = Math.floor(Math.random()*3+1);
      
      let result = getdb(
        Number(randomCategory),
        Number(randomPoint)
      );
      result
        .then((res: any) => {
          this.word = res;
          resolve(res);
        })
        .catch((error) => reject(error));
    resolve(1);
    });
    return iWillSetWord;
  };

  changeWord() {
    this.setWord();
  }

  initializeGame() {
    this.turn = 1;
  }

  initializeRound() {
    this.time = configQuickInstance.gameTime;
    this.started = false;
    this.gameFinished = false;
  }

  play() {
    this.started = true;
    this.setWord();
    this.interval = setInterval(() => {
      if (this.time > 0) {
        this.time--;
      }
      if (this.time === 0) {
        this.end();
      }
    },1000);
  }

  end() {
    this.started = false;
    this.checkTurn();
    clearInterval(this.interval);
  }

  checkTurn () {
    if (this.teams.length > this.turn) {
      this.turn++;
      this.time = configQuickInstance.gameTime;
      this.initializeRound();
    } else {
      this.gameFinished = true;
      this.setWinner();
    } 
  }

  handleAnswer (index: number,isCorrect: boolean) {
    if(isCorrect) {
      this.teams[index-1].score++;
    }
    this.setWord();
  }

  setWinner() {
    let groupScores: any = [];
    this.teams.map((item: any) => {
      groupScores.push(item.score);
    });
    const maxScore: number = Math.max(...groupScores);

    if (this.checkDuplicate(maxScore, groupScores) > 1) {
      this.winner = "بازی مساوی شد";
    } else {
      this.teams.map((item: any) => {
        if (item.score == maxScore) {
          this.winner = item.name + " برنده شد ";
        }
      });
    }
  }

  private checkDuplicate(maxScore: number, groupScores: any) {
    let duplicate: number = 0;

    for (let i = 0; i < this.teams.length; i++) {
      if (groupScores[i] == maxScore) {
        duplicate++;
      }
    }

    return duplicate;
  }
}

export default Game_Quick;
