import Team from "./team";
import getdb from "../database/getdb";
import { configFightInstance, gameFightInstance, teamsInstance } from "./index";
import Config_Fight from "./config_fight";
import Teams from "./teams";

class Game_Normal {
  teams: Team[];
  laps: number;
  lapsTime: number;

  roundStarted: boolean = false;
  roundFinished: boolean = false;
  gameFinished: boolean = false;
  turn: number = 1;
  word: string = "";
  currentLap: number = 1;
  interval: number = 0;
  winner: string = "";
  
  constructor() {
    this.teams = teamsInstance.teams;
    this.laps =  teamsInstance.teams.length;
    this.lapsTime = configFightInstance.laps_time;
  }

  configure(configurations: Config_Fight,teams: Teams) {
    this.teams = teams.teams;
    this.gameFinished = false;
    this.lapsTime = configFightInstance.laps_time;
    this.currentLap = 1;
  }

  setWord = (): Promise<any> => {
    let iWillSetWord = new Promise((resolve, reject) => {
      let randomCategory = Math.floor(Math.random()*20+1);
      let randomPoint = Math.floor(Math.random()*2+1);

      let result = getdb(Number(randomCategory), Number(randomPoint));
      result
        .then((res: any) => {
          this.word = res;
          resolve(res);
        })
        .catch((error) => reject(error));

      resolve(1);
    });

    return iWillSetWord;
  };

  initializeRound() {
    this.roundStarted = false;
    this.roundFinished = false;
    this.gameFinished = false;
    this.lapsTime = configFightInstance.laps_time;
  }

  play() {
    this.roundStarted = true;
    this.setWord();
    this.interval = setInterval(() => {
      if (this.lapsTime > 0) {
        this.lapsTime--;
      }
      if (this.lapsTime === 0) {
        this.roundFinish();
      }
    }, 1000);
  }

  roundFinish() {
    this.roundFinished = true;
    this.handlePoint(1,this.turn-1);
    this.setWinner();
    this.laps =  teamsInstance.teams.length - 1;
    if (this.currentLap === this.laps) {
      this.gameFinished = true;
    } else {
      this.currentLap++;
    }
    this.turn = 1;
    clearInterval(this.interval);
  }

  changeTurn() {
    if (this.teams.length > this.turn) {
      this.turn++;
    } else {
      this.turn = 1;
    }
  }

  handlePoint(point: number, index: number) {
    let updatedTeams = [];

    for (let i = 0; i < this.teams.length; i++) {
      if (index !== i) {
        updatedTeams.push(this.teams[i]);
      }
      else {
        let updatedTeam = new Team(
          this.teams[i].name,
          this.teams[i].score + Number(point)
        );
        updatedTeams.push(updatedTeam);
      }
    }
    this.teams = updatedTeams;
  }

  setWinner() {
    // let groupScores: any = [];
    let updatedTeams = [];
    for (let i = 0; i < this.teams.length; i++) {
      if (this.teams[i].score === 0) {
        updatedTeams.push(this.teams[i]);
      }
    }
    this.teams = updatedTeams;
    if (this.teams.length === 1) {
      this.winner = this.teams[0].name;
    }
  }
}

export default Game_Normal;
